import { Component, OnInit, Input } from '@angular/core';
import { WorkBoardDataService } from 'src/app/services/work-board-data.service';

@Component({
  selector: 'app-work-column',
  templateUrl: './work-column.component.html',
  styleUrls: ['./work-column.component.css']
})
export class WorkColumnComponent implements OnInit {

  columns: any[];
  addBlockState = true;
  @Input() data: any;
  listActionsState = false;
  addItemToTopState = false;

  constructor(
    private boardData: WorkBoardDataService,
  ) {
    
   }

  ngOnInit() {
    this.columns = this.boardData.getWorkColumns();
  }

  addItem(text){
    if(text.value.trim() == ''){
      return false
    }
    this.boardData.addListItem(this.data.id, text.value);
    text.value = '';
  }
  addItemToTop(text){
    if(text.value.trim() == ''){
      return false
    }
    this.boardData.addListItemToTop(this.data.id, text.value);
    text.value = '';
  }
  action(event){
    if(event.id == 0){
      this.addCard(event.value)
    }
  }
  addCard(value){
    this.addItemToTopState = value;
  }
  close(event){
    this.listActionsState = event;
  }
  sortByParam(id){
    if(id == 0){
      
    }
  }
  

}
