import { Injectable } from '@angular/core';
import {Column} from '../models/column'

@Injectable({
  providedIn: 'root'
})
export class WorkBoardDataService {
  workColumnsIndex = 0;
  listIndex = 0;
  constructor() { }
  
  boards = [
    {id: 0, name: 'One page'},
  ]
  workColumns = [
    {id: 0, name: 'qwerty',
      listItems: [
        {text: 'asdfgb', itemId: 0, changed: false, date: 0},
        {text: ';lkkjsnv', itemId: 1, changed: false, date: 0},
      ]
    },
    {id: 2, name: 'qwerty2',
      listItems: [
        {text: '2 asdfgb', itemId: 0, changed: false, date: 0},
        {text: '2 ;lkkjsnv', itemId: 1, changed: false, date: 0},
      ]
    },
    {id: 3, name: 'qwerty3',
      listItems: [
        {text: '3 asdfgb', itemId: 0, changed: false, date: 0},
        {text: '3 ;lkkjsnv', itemId: 1, changed: false, date: 0},
      ]
    },
  ]
  
// -- Board
  getBoards(){
    return this.boards;
  }
  getBoardById(id){
    for(let i=0; i<this.boards.length; i++){
      if(this.boards[i].id === id){
        return this.boards[id].id;
      }
    }
  }
// -- Work-Board
  getWorkColumns(){
    return this.workColumns;
  }
  addListItem(id, textValue){
    this.listIndex++
    for(let i=0; i<this.workColumns.length; i++){
      if(this.workColumns[i].id === id){
        const item = {
            itemId: this.listIndex,
            text: textValue,
            changed: false,
            date: Date.now(),
          };
        this.workColumns[i].listItems.push(item);
        console.log(this.workColumns[i].listItems);
      }
    }
  }

  addListItemToTop(id, textValue){
    for(let i=0; i<this.workColumns.length; i++){
      if(this.workColumns[i].id == id){
        const item = 
          {
            itemId: this.listIndex++,
            text: textValue,
            changed: false,
            date: Date.now(),
          };
        this.workColumns[i].listItems.unshift(item);
      }
    }
  }
  
  addWorkInColumns(name){
    if(name.trim() == ""){
      return false;
    }
    this.workColumnsIndex++
    const item = {
      id: this.workColumnsIndex,
      name: name,
      listItems: [],
      date: Date.now(),
    }
    this.workColumns.push(item)
  }

  consolelog(){
    console.log(this.workColumns);
  }

  copyNewColumn(event){
    let item = {...event};
    this.workColumnsIndex++
    item.id = this.workColumnsIndex;
    item.date = Date.now();
    this.workColumns.push(item);
    this.consolelog()
  }
  sortByParam(item, id){
    console.log(item);

    //сортируем строки по названию
    if(item == 3){
      this.workColumns[id].listItems.sort(function (a, b) {
        var nameA = a.text.toLowerCase(), nameB = b.text.toLowerCase()
        if (nameA < nameB) {
          return -1
        }
        if (nameA > nameB) {
          return 1
        }
      });
    }
    if(item == 1){
      this.workColumns[id].listItems.sort(function (a, b){
        // return a.date - b.date
        return 1
      })
    }
    if(item == 0){
      
      this.workColumns[id].listItems.sort(function (a, b){
        // return b.date - a.date
        return 1

      })
    }
  }

  moveTo(items, id){
    console.log('move', 0);
    for(let i=0; i<this.workColumns.length; i++){
      if(this.workColumns[i].id == id){
        console.log(items);
        console.log(this.workColumns[i].listItems);
        console.log(123);
        this.workColumns[i].listItems.concat(items)
        console.log(this.workColumns[i].listItems);
        
      }
    }
    console.log('move', 1);
    let a =[0, 1]
    let b = [2, 3]
    console.log(a.concat(b));
    
  }
  
  deleteWorkColumnItemById(id, arr){
    for(let i=0; i<this.workColumns[id].listItems.length; i++){
      for(let j=0; j<arr.length; j++){
        if(this.workColumns[id].listItems[i] == arr[j]){
          this.workColumns[id].listItems.splice(i, 1);
        }
      }
    }
  }
  
  
  
  
  
  
  
}
