import { Component, OnInit } from '@angular/core';
import { WorkBoardDataService } from 'src/app/services/work-board-data.service';

@Component({
  selector: 'app-work-board',
  templateUrl: './work-board.component.html',
  styleUrls: ['./work-board.component.css']
})
export class WorkBoardComponent implements OnInit {

  columns: any[];
  addingBlockState = false;

  constructor(
    private boardDate: WorkBoardDataService,
  ) { }


  ngOnInit() {
    this.columns = this.boardDate.getWorkColumns();
  }

  addColumn(input){
    if(input.value.trim() == ''){
      return false
    }
    this.boardDate.addWorkInColumns(input.value)
    input.value = ''
  }
  
  
  

}
