import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { WorkBoardDataService } from 'src/app/services/work-board-data.service';

@Component({
  selector: 'app-list-actions',
  templateUrl: './list-actions.component.html',
  styleUrls: ['./list-actions.component.css']
})
export class ListActionsComponent implements OnInit {

  title = 'Действия со списком';
  actionStateId = 0;
  @Input() data: any;
  @Output() action = new EventEmitter();
  @Output() close = new EventEmitter();
  newName = '';
  workColumns: any;
  listActions = [
    [
      {id:0, text: 'Добавить карточку…'},
      {id:1, text: 'Копировать список…'},
      {id:2, text: 'Переместить список…'},
      {id:3, text: 'Подписаться '},
    ],
    [
      {id:4, text: 'Сортировать по...'},
    ],
    [
      {id:5, text: 'Переместить все карточки списка…'},
      {id:6, text: 'Архивировать все карточки списка…'},
    ],
    [
      {id:7, text: 'Архивировать список'},
    ]
  ]

  constructor(private boardData: WorkBoardDataService) { 
    this.workColumns = this.boardData.getWorkColumns()
  }
  ngOnInit() {
    this.newName = this.data.name;
  }

  someAction(action){
    if(action.id == 0){
      this.addCard(action.id )
    }
    if(action.id == 1){
      this.copyCard(action.id)
    }
    if(action.id == 4){
      this.sortBy(action.id)
    }
    if(action.id == 5){
      this.moveTo(action.id)
    }
  }
  defaulActionState(){
    this.actionStateId = 0;
    this.title = 'Действия со списком';
  }
  closeEmit(){
    const item = false;
    this.close.emit(item)
  }
  complateClose(){
    this.closeEmit();
    this.defaulActionState();
  }
  addCard(id){
    const item = {
      id: id,
      value: true,
    }
    this.action.emit(item)
  }
  // копирование карточки
  copyCard(id){
    this.actionStateId = id;
    this.title = 'Копирование списка';
  }
  copyColumn(text){
    if(text.trim() == ''){
      return false
    }
    let item0 = JSON.stringify(this.data);
    let item = JSON.parse(item0);
    item.name = text;
    this.boardData.copyNewColumn(item);
    this.defaulActionState();
    this.closeEmit();
  }
  // сортировка карточки
  sortBy(id){
    if(this.data.listItems.length == 0){
      return false;
    }
    this.actionStateId = id;
    this.title = 'Сортировать колонку';
  }
  sortByParam(item, id){
    console.log(this.data);
    this.boardData.sortByParam(item, this.data.id);
    this.complateClose();
  }
  
  moveTo(id){
    this.actionStateId = id;
    this.title = 'Переместить все карточки в список';
  }
  moveToService(id){
    let items0 = JSON.stringify(this.data.listItems);
    let items = JSON.parse(items0);
    this.boardData.moveTo(items, id);
    let idArr = [];
    for(let i=0; i<this.data.listItems.length; i++){
      idArr.push(this.data.listItems[i]);
    }
    this.boardData.deleteWorkColumnItemById(this.data.id, idArr)
  }
  

}
